import vue from '../main'
import store from '../store/index'
import { Loading, Message, Notification } from 'element-ui'
import {listenTf, batteryWarning, globalPlan} from './methods'
import { PARAMS } from "./params"
import { gitMapMethods, queryBranch } from './branch'
import { displayMap } from './map'
import { agvToServerFn, serverRosInit } from './websocket' 
import { ip } from './config'
export var ros, viewer, zoomview, ns = '', startTime, role, taskStateType, taskStateMessage, NEW_MAP = new Map(), serverConStatus = false;
export var OBJ = new DataServer();

function DataServer() {
    // TODO
};
//继承DATA属性，获取：DATA.DataServer.
DataServer.prototype.register = function (key, value, options) {
    if (this.hasOwnProperty(key)) {
        return;
    }
    var fnGetDefault = function () {
        return value;
    };
    var fnSetDefault = function (val) {
        if (val === value) {
            return;
        }
        if (val.hasOwnProperty('property')) {
            value = val.value;
        }
        else {
            value = val;
        }
    };
    var fnGet = options.fnGet || fnGetDefault;
    var fnSetOptions = options.fnSet || function () { };
    var fnSet = function (val) {
        fnSetDefault(val);
        fnSetOptions(val);
    };
    //Object.defineProperty：添加或修改属性
    //对象，属性名字，属性特征
    Object.defineProperty(this, key, {
        get: fnGet,
        set: fnSet
    });
};

DataServer.prototype.syncToLocalStorage = function () {
    //TODO
};

function registerObj() {
    return new Promise(function (resolve, reject) {
        OBJ.register('topic', {}, {});
        OBJ.register('wsTopic', {}, {});
        OBJ.register('tfMsg', {}, {});
        OBJ.register('cmdStringTopic', {}, {});
        OBJ.register('mapMsg', {}, {});
        OBJ.register('mapInfo', null, {});
        OBJ.register('navCtrlStatus', null, {});
        OBJ.register('mapStage', null, {});
        OBJ.register('wayPointsMsg', {}, {});
        OBJ.register('robotPoseMsg', {}, {});//add waypoints use
        OBJ.register('robotStage', {}, {});//robot maker
        OBJ.register('waypointsMessage', {}, {});
        OBJ.register('waypointsStage', {}, {});
        OBJ.register('mapXY', {}, {});
        OBJ.register('imgMapStage', {}, {});
        OBJ.register('laserScanStage', {}, {});
        OBJ.register('laserScanPose', {}, {});
        OBJ.register('footprintStage', {}, {});
        OBJ.register('globalPlanStage', {}, {});
        OBJ.register('tabLocalPlanStage', {}, {});
        OBJ.register('taskState', {}, {});
        OBJ.register('initialPoseStage', {}, {});
        
        resolve('register OBJ');
    })
}

//初始化ros
function rosInit() {
    return new Promise(function (resolve, reject) {
        ros = new ROSLIB.Ros({
            url: 'ws://' + ip + ':9090',
        });

        //链接到websocket服务器
        ros.on('connection', () => {
            console.log(`[INFO]Connected to rosbridge ${ip}.`);
            resolve('ros Connected');
            PARAMS.connecting = true;
            clearTimeout(PARAMS.reconnectSettimeout);
            if(PARAMS.connectFlg){
                window.location.reload();
            }
        });
        //关闭websocket服务器
        ros.on('close', () => {
            console.log(`[INFO]Rosbridge server ${ip} closed.`);
            Notification({
                title: "错误",
                message: "Rosbridge连接已关闭",
                type: 'error',
                position: 'top-left',
                offset: 100
            });                
            reject('Rosbridge连接已关闭');
            PARAMS.connecting = false;
            reconnect()
        });
        //关闭websocket服务器出错
        ros.on('error', (e) => {
            Notification({
                title: "错误",
                message: "Rosbridge连接失败",
                type: 'error',
                position: 'top-left',
                offset: 100
            });
            console.log("[INFO] rosbridge error" + e)
            reject('Rosbridge连接失败');
            PARAMS.connecting = false;
            reconnect()
        });
        role = window.localStorage.getItem('role');
        role == 'developer' && store.dispatch("setUserRole","developer");
        role == 'Administrator' && store.dispatch("setUserRole","Administrator");
    })
}

function closeWS(){
    window.onbeforeunload = (e) => {
        if (!(event.clientX > document.body.clientWidth && event.clientY < 0) || !event.altKey) {
            ros && ros.close();
        }
      }
}
//检测重连
function reconnect () {
    PARAMS.connectFlg = true;
    if (PARAMS.connecting) {
        return
    }
    PARAMS.reconnectSettimeout && clearTimeout(PARAMS.reconnectSettimeout)
    PARAMS.reconnectSettimeout = setTimeout(() => {
        rosInit()
    }, PARAMS.timeout)
}

//initStage
function initStage() {
    return new Promise(function (resolve, reject) {
        var screenWidth = window.innerWidth || document.body.clientWidth || document.documentElement.clientWidth;
        var screenHeight = window.innerHeight || document.body.clientHeight || document.documentElement.clientHeight;
        var rMap = screenWidth / screenHeight;//比例
        var width = screenWidth;
        var height = screenHeight;
        if (screenWidth > screenHeight) {
            width = screenHeight * rMap;
            height = screenHeight;
        } else {
            width = screenWidth;
            height = screenWidth / rMap;
        }

        OBJ.mapInfo = {
            windowWidth: width,
            windowHeight: height
        };

        // Create the main viewer.
        viewer = new ROS2D.Viewer({
            divID: 'mapNavDiv',
            width: OBJ.mapInfo.windowWidth,
            height: OBJ.mapInfo.windowHeight
        });
        zoomview = new ROS2D.ZoomView({
            rootObject: viewer.scene
        });

        //实例化画布对象
        var stage = viewer.scene;
        createjs.Touch.enable(stage);//开启触摸，disable禁止触摸
        createjs.Ticker.setFPS(25);//一秒25帧

        /**
         * 拖拽移动画布
         * **/
        stage.enableMouseOver(10);//用到mouseover要加上这一句

        createjs.Touch.enable(stage);//移动端也支持点击移动事件，允许设备触控

        stage.mouseMoveOutside = true;//鼠标离开画布继续调用鼠标移动事件

        stage.on("mousedown", function (evt) {
            if (store.state.disabledMoveMap) {
                return
            } else {
                // keep a record on the offset between the mouse position and the container position. currentTarget will be the container that the event listener was added to:
                evt.currentTarget.offset = { x: this.x - evt.stageX, y: this.y - evt.stageY };
            }
        });
        stage.on("pressmove", function (evt) {
            if (store.state.disabledMoveMap) {
                return
            } else {
                // Calculate the new X and Y based on the mouse new position plus the offset.
                evt.currentTarget.x = evt.stageX + evt.currentTarget.offset.x;
                evt.currentTarget.y = evt.stageY + evt.currentTarget.offset.y;
                // make sure to redraw the stage to show the change:
                stage.update();
            }
        });
        stage.on('mousewheel',(event)=>{
        })
        stage.update();

        //刻度事件，实时监听变化
        createjs.Ticker.addEventListener('tick', function () {
            //更新，重新绘制画布
            stage.update();
        });
        resolve('initStage success')
    })
}

function getNS(){
    return new Promise(function (resolve, reject) {
        // namespeace topic
        var diagnosticsAggTopic = new ROSLIB.Topic({
            ros: ros,
            name: '/diagnostics_agg',
            messageType: 'diagnostic_msgs/DiagnosticArray'
        });
        OBJ.topic['/diagnostics_agg'] = diagnosticsAggTopic;
        diagnosticsAggTopic.subscribe((res) => {
            var STATUS_OBJ = {}, aggStatus = [], nsList = []; 
            //时间戳
            store.commit("stamp", res.header.stamp)
            res.status.map((item)=>{
                NEW_MAP.set(String(item.name), item)
                STATUS_OBJ[item.name] = item;
                var numX = item.name.lastIndexOf('/')
                if(numX != 0){
                    aggStatus.push({'label': item.name, 'levelnum': item.level, 'parName': item.name.substr(0, numX)})
                }else{
                    aggStatus.push({'label': item.name, 'levelnum': item.level, 'parName': ""})
                    nsList.push(item.name);
                }
            })
            if(!nsList.includes('/DEVICES') && nsList.length > 0){
                ns = nsList[0];
            }
            // if(NEW_MAP.has(ns + "/GIT/dbparam")){
            //     gitMapMethods(STATUS_OBJ["/GIT/dbparam"])
            // }
            if(NEW_MAP.has(ns + '/ALGORITHM/AMCL/amcl_reflection_class')){
                if(STATUS_OBJ[ns + '/ALGORITHM/AMCL/amcl_reflection_class']['level'] == 0){
                    store.commit('amclReflectionFLag', 0)
                }else{
                    store.commit('amclReflectionFLag', 1)
                }
            }
            if(NEW_MAP.has(ns + '/ALGORITHM/AMCL/amcl_origin_class')){
                if(STATUS_OBJ[ns + '/ALGORITHM/AMCL/amcl_origin_class']['level'] == 0){
                    store.commit('amclOriginFLag', 0)
                }else{
                    store.commit('amclOriginFLag', 1)
                }
            }
            NEW_MAP.clear();
            store.commit("setStatusArray", aggStatus)
            store.state.serverContented && agvToServerFn('/diagnostics_agg', res);
            resolve('get NS for agg')
        });
    })
}

/**init topics
 * cmdStringTopic   -   提交查询地图话题
 * feedbackTopic    -   订阅地图和系统更新反馈
 * **/
function initTopics(){
    return new Promise(function(resolve, reject){
        let list = JSON.parse(window.localStorage.getItem('wsTopicConfig'))
        list.forEach(item => {
            let topicName = item.name;
            OBJ.topic[topicName] = new ROSLIB.Topic({
                ros: ros,
                name: item.name == '/diagnostics_agg' ? '/diagnostics_agg' : ns + item.name,
                messageType: item.type
            });
            OBJ.topic[topicName].advertise();
            switch (topicName) {
                case '/robot_pose':
                    OBJ.topic[topicName].subscribe(function(msg){
                        OBJ.robotPoseMsg = msg;
                    })
                    break;
                case '/task_state':
                    OBJ.topic[topicName].subscribe(function(msg){
                        msg.values.map(function (e, i) {
                            if (e.key === 'auto_charge') {
                                store.commit('chargeStatus', e.value);
                                switch (e.value) {
                                    case '0':
                                        store.commit("switchBatteryStatus", false);
                                        break;
            
                                    case '1':
                                        store.commit("switchBatteryStatus", true);
                                        break;
                                }
                            } else if (e.key === 'cartographer') {
                                OBJ.taskState = e.value;
                                if (e.value === "0") {
                                    store.commit("setRosMode", PARAMS.rosMode.navigation);
                                } else if (e.value === "1") {
                                    store.commit("setRosMode", PARAMS.rosMode.gmapping);
                                } else {
            
                                }
                            }
                        })
                    })
                break;
                case '/battery':
                    OBJ.topic[topicName].subscribe(function(msg){
                        if(msg.percentage){
                            let butteryNum = Math.round(msg.percentage * 100);
                            if( 0 <= butteryNum <= 100){
                                if (butteryNum <= 15) {
                                    if(store.state.batteryStatus){
                                        store.commit('batteryState', PARAMS.butteryStatus.charging);
                                        batteryWarning();
                                    }else{
                                        store.commit('batteryState', PARAMS.butteryStatus.warning);
                                        batteryWarning();
                                    }
                                } else {
                                    if(store.state.batteryStatus){
                                        store.commit('batteryState', PARAMS.butteryStatus.charging);
                                    }else{
                                        store.commit('batteryState', PARAMS.butteryStatus.success);
                                    }
                                }
                                store.commit('subButtery', butteryNum);
                                store.commit('showBattery', true)
                            }
                        }
                    })
                break;
                case '/task_switch':
                    OBJ.topic[topicName].subscribe(function(res){
                        if(res.frame_id.startsWith("update_response")){
                            switch(res.seq){
                                case 1:
                                    Loading.service().close();
                                    Notification({
                                        title: "成功",
                                        message: "更新成功,请重启系统",
                                        type: 'success',
                                        position: 'top-left',
                                        offset: 100
                                    });
                                    Message({
                                        message: "更新成功",
                                        type: "success",
                                        center: true,
                                        duration: 0,
                                        showClose: false,
                                        customClass: "bottomTaskStatus"
                                    });
                                    break;
                                case 0:
                                    Loading.service().close();
                                    Notification({
                                        title: "错误",
                                        message: res.frame_id.replace(/update_response_/, ""),
                                        type: 'error',
                                        position: 'top-left',
                                        offset: 100
                                    });
                                    Message({
                                        message: "更新失败,失败原因：" + res.frame_id.replace(/update_response_/, ""),
                                        type: "error",
                                        center: true,
                                        duration: 0,
                                        showClose: false,
                                        customClass: "bottomTaskStatus"
                                    });
                                    break;
                                default:
                                    break;
                            }
                        }
                        if(res.frame_id.startsWith("slam")){
                            switch(res.seq){
                                case 1:
                                    window.localStorage.setItem('mappingStatus', '1');
                                    break;
                                case 0:
                                    window.localStorage.setItem('mappingStatus', '0');
                                    break;
                                default:
                                    break;
                            }
                        }
                    })
                break;
                case '/flexbe/behavior_list':
                    OBJ.topic[topicName].subscribe(function(msg){
                        var list = JSON.parse(msg.data.replace(/\r|\n/ig,"").replace(/\r|\t/ig,""));
                        store.commit("flexBeList", list)
                    })
                break;
                case '/flexbe/behavior_updated':
                        OBJ.topic[topicName].subscribe(function(message){
                            OBJ.behaviorUpdateStatus = message;
                            // let status = "{\"10\": \"shelf_navigation->d10000one\", \"1\": \"shelf_navigation->done\", \"2\": \"\
                            // shelf_navigation->done\", \"3\": \"shelf_navigation->done\", \"4\": \"shelf_navigation->done\"\
                            // , \"5\": \"shelf_navigation->S-1\", \"6\": \"shelf_navigation->done\", \"7\": \"\
                            // shelf_navigation->done\", \"8\": \"shelf_navigation->done\", \"9\": \"shelf_navigation->done\"\
                            // , \"0\": \"shelf_navigation->S-1\"}"
                            // let statusJSON = JSON.parse(status)
                            // console.log(Object.keys(statusJSON).length)
                            window.localStorage.setItem('flexBeTaskStatus', message.data);
                            if(message.data === "behavior_entrance"){
                                taskStateType = "info";
                                taskStateMessage = "进入任务模式"
                                Notification({
                                    title: "成功",
                                    message: "发布任务成功",
                                    type: 'success',
                                    position: 'top-left',
                                    offset: 100
                                });
                            }else if(message.data === "behavior_exit" || message.data === ""){
                                taskStateType = "info";
                                taskStateMessage = "空闲模式"
                            }else {
                                taskStateType = "info";
                                taskStateMessage = `正在执行任务: ${message.data}`
                            }
                            Message({
                                message: taskStateMessage,
                                type: taskStateType,
                                center: true,
                                duration: 0,
                                showClose: false,
                                customClass: "bottomTaskStatus"
                            });
                        })
                    break;
                default:
                    break;
            }
            if(item.subFlag == 'sub'){
                OBJ.topic[topicName].subscribe(function(msg){
                    store.state.serverContented &&  agvToServerFn(topicName, msg);
                })
            }
        });
        queryBranch();   
        resolve('init topics')
    })
}

function promiseFun(...params){
    console.log(params)
    Loading.service().close();
}
$(() => {
    startTime = new Date()
    Loading.service({
        text: "加载中..."
    })
    async function initSystem(){
        try{
            const closeWSFun       = await closeWS();
            const register         = await registerObj();
            const rosInitFun       = await rosInit();
            const listenTfFun      = await listenTf();
            const nsFN             = await getNS();
            const initTopicsFun    = await initTopics();
            const initStageFun     = await initStage();
            const displayMapFun    = await displayMap();
            const globalPlanFun    = await globalPlan();
            const serverRosInitFun = await serverRosInit();
            promiseFun(closeWSFun, register, rosInitFun, nsFN, listenTfFun, initTopicsFun, initStageFun, displayMapFun, globalPlanFun, serverRosInitFun)
        }catch(err){
            Loading.service().close();
            console.log(err)
        }
    }
    initSystem()
});




